.product-detail {
	padding-top: 52px;
	padding-bottom: 160px;
}
.product-detail__info {
	.product-title {
		h2 {
			display: inline-block;
			font-size: 32px;
			font-weight: 600;
			color: $color;
			margin: 0;
		}
		.hotel-star {
			display: inline-block;
			font-size: 10px;
			color: $color;
			margin-left: 10px;
		}
	}
	.product-address {
		font-size: 14px;
		color: #666;
		margin-bottom: 7px;
	}
	.product-email {
		.fa {
			color: $color;
			font-size: 15px;
			margin: 0 5px 0 2px;
		}
		a {
			color: #666;
			&:hover {
				color: $color;
			}
		}
	}
	.property-highlights {
		margin-top: 25px;
		.property-highlights__content {
			overflow: hidden;
			margin-left: -10px;
			margin-right: -10px;
		}
		h3 {
			font-size: 20px;
			font-weight: 600;
			color: $color;
			border-bottom: 2px solid #D4D4D4;
			padding: 8px 0;
			margin-top: 0;
		}
		.item {
			width: 33.3333333333%;
			float: left;
			color: #666;
			padding: 9px 10px;
			.awe-icon {
				float: left;
				font-size: 18px;
				margin-top: 1px;
			}
			span {
				display: block;
				margin-left: 36px;
			}
		}
	}
	.rating-trip-reviews {
		margin: 12px -10px;
		overflow: hidden;
		.item {
			width: 33.3333333333%;
			float: left;
			padding: 0 10px;
			margin-top: 10px;
			.count {
				display: inline-block;
				width: 40px;
				height: 40px;
				text-align: center;
				line-height: 40px;
				border-radius: 4px;
				font-weight: 600;
				font-size: 16px;
				color: #fff;
				float: left;
				margin-right: 10px;
			}
			&.good {
				.count {
					background-color: #55A649;
				}
			}
			&.fine {
				.count {
					background-color: #F27C38;
				}
			}
			h6 {
				font-size: 12px;
				font-weight: 700;
				color: #666;
				margin: 0;
			}
			p {
				font-size: 16px;
				color: #A6A6A6;
				margin-bottom: 0;
			}
		}
	}
	.product-descriptions {
		font-size: 14px;
		color: #666;
		margin-top: 30px;
	}
	.trips {
		overflow: hidden;
		margin-top: 32px;
		margin-left: -15px;
		margin-right: -15px;
		.item {
			padding: 13px 15px;
			float: left;
			width: 33.3333333333%;
			h6 {
				font-weight: 700;
				font-size: 12px;
				color: #262626;
				margin-top: 0;
				margin-bottom: 5px;
			}
			p {
				font-size: 14px;
				color: #666;
				margin-bottom: 0;
				font-weight:300;
				// text-overflow: ellipsis;
				// overflow: hidden;
				white-space: nowrap;
				.fa,
				.awe-icon {
					margin-right: 8px;
					font-size: 14px;
					color: #A6A6A6;
				}
			}
		}
	}
}


.ticket-price {
	margin-top: 45px;
	th, td {
		padding: 10px 25px;
		border-right: 2px dotted #A6A6A6;
		&:last-child {
			border-right: 0;
		}
	}
	.ticket-price {
		padding-left: 0;
	}
	.kid {
		padding-right: 0;
	}
	thead {
		th {
			p {
				font-size: 20px;
				font-weight: 600;
				color: $color;
				margin: 0;
			}
			span {
				font-size: 12px;
				font-weight: 600;
				color: #666;
			}
		}
	}
	tbody {
		tr {
			border-top: 2px solid #A6A6A6;
			td {
				vertical-align: top;
				em {
					font-style: normal;
					font-size: 12px;
					color: #666;
				}
				ins {
					display: block;
					text-decoration: none;
					.amount {
						font-size: 20px;
						color: $color;
					}
				}
				del {
					.amount {
						font-size: 14px;
						color: #606060;
					}
				}
			}
		}
	}
}

.trip-schedule-accordion {
	.ui-accordion-content {
		ol, ul {
			li {
				padding-top: 4px;
				padding-bottom: 4px;
			}
		}
	}
	.trips {
		.item {
			max-width: 190px;
			display: inline-block;
			padding-right: 30px;
			h6 {
				font-size: 12px;
				font-weight: 700;
				color: #666;
				margin-top: 0;
				margin-bottom: 5px;
			}
			p {
				font-size: 14px;
				color: #666;
				margin-bottom: 0;
				font-weight:300;
				// text-overflow: ellipsis;
				// overflow: hidden;
				white-space: nowrap;
				.fa,
				.awe-icon {
					margin-right: 8px;
					font-size: 18px;
					color: #A6A6A6;
				}
			}
		}
	}
	.tour-map-wrapper {
		overflow: hidden;
		h6 {
			font-size: 12px;
			font-weight: 700;
			color: #666;
			margin-top: 0;
			margin-bottom: 5px;
		}
	}
}

.product-slider-wrapper {
	overflow: hidden;
}
.product-slider {
	.item {
		img {
			width: 100%;
		}
	}
	.owl-controls {
		.owl-buttons {
			position: static;
			.owl-prev {
				position: absolute;
				top: 50%;
				left: -30px;
				@include translateY(-50%);
				z-index: 999;
				opacity: 0;
				@include transition;
			}
			.owl-next {
				position: absolute;
				top: 50%;
				right: -30px;
				@include translateY(-50%);
				z-index: 999;
				opacity: 0;
				@include transition;
			}
		}
	}
	&:hover {
		.owl-controls {
			.owl-buttons {
				.owl-prev {
					left: 5px;
					opacity: 1;
				}
				.owl-next {
					right: 5px;
					opacity: 1;
				}
			}
		}
	}
}
.product-slider-thumb-row {
	margin-left: -8px;
	margin-right: -8px;
}
.product-slider-thumb {
	padding: 8px 0;
	.owl-item {
		padding: 8px;
		@include transition;
		&.synced {
			opacity: .4;
		}
	}
	.item {
		cursor: pointer;
		background-color: #ddd;
		img {
			display: block;
			width: 100%;
		}
	}
}

.product-tabs {
	margin-top: 62px;
}
.product-tabs__content {
	.ui-tabs-panel {
		overflow: hidden;
		overflow-x: auto;
		min-height: 500px;
		.check-availability {
			background-color: #fff;
			padding: 20px;
			overflow: hidden;
			margin-bottom: 22px;
			label {
				display: block;
				font-size: 13px;
				font-weight: 600;
				color: #666;
				margin-bottom: 8px;
			}
			.form-elements {
				float: left;
				input,
				select {
					background-color: #eee;
					width: 100%;
					height: 36px;
					line-height: 36px;
				}
				&.form-checkin {
					width: 16%;
					margin-right: 2%;
				}
				&.form-checkout {
					width: 16%;
					margin-right: 4%;
				}
				&.form-adult {
					width: 16%;
					margin-right: 2%;
				}
				&.form-kids {
					width: 16%;
					margin-right: 2%;
				}
				.form-item {
					position: relative; 
				}
				span {
					display: inline-block;
					font-size: 10px;
					font-weight: 600;
					color: #666;
					margin-top: 6px;
				}
				.awe-icon {
					position: absolute;
					width: 36px;
					height: 34px;
					background-color: #eee;
					line-height: 34px;
					text-align: right;
					top: 1px;
					right: 1px;
					padding-right: 12px;
					font-size: 16px;
					color: #666;
					pointer-events: none;
				}
				.fa {
					background-color: #eee;
				}
				.awe-select-wrapper {
					width: 100%;
				}
			}
			.form-actions {
				float: left;
				width: 22%;
				margin-left: 4%;
				margin-top: 26px;
				.awe-btn {
					font-size: 13px;
					font-weight: 600;
					width: 100%;
					padding: 9px 16px;
				}
			}
		}
	}
}
.initiative {
	min-width: 680px;
}

.room-type-table {
	width: 100%;
	min-width: 650px;
	.room-type {
		width: 53%;
		padding-right: 30px;
	}
	.room-people {
		width: 11.5%;
		padding-right: 30px;
	}
	.room-condition {
		width: 20%;
		padding-right: 30px;
	}
	.room-price {
		width: 14.5%;
		padding-right: 30px;
	}
	thead {
		th {
			font-size: 14px;
			font-weight: 600;
			color: #666;
			border-bottom: 2px solid #A6A6A6;
			padding: 8px 0;
		}
	}
	tbody {
		td {
			vertical-align: top;
			padding-top: 20px;
			padding-bottom: 20px;
			border-bottom: 1px solid #dcdcdc;
			&.room-type {
				.room-thumb {
					width: 140px;
					float: left;
					margin-right: 30px;
					img {
						width: 100%;
					}
				}
				.room-title {
					h4 {
						font-weight: 700;
						font-size: 16px;
						margin: 0;
						a {
							color: #262626;
							&:hover {
								color: $color;
							}
						}
					}
				}
				.room-descriptions {
					font-weight: 600;
					font-size: 13px;
					color: #666;
					p {
						line-height: 1.6em;
					}
				}
				.room-type-footer {
					.awe-icon {
						font-size: 11px;
						color: #666;
						margin-right: 14px;
					}
				}
			}
			&.room-people {
				.awe-icon {
					font-size: 20px;
					color: #666;
				}
			}
			&.room-condition {
				.list-condition {
					margin: 0;
					padding-left: 20px;
					li {
						font-weight: 600;
						font-size: 13px;
						color: #666;
						padding-bottom: 6px;
					}
				}
			}
			&.room-price {
				.price {
					.amount {
						display: block;
						font-weight: 700;
						font-size: 24px;
						color: #666;
						line-height: normal;
					}
					em {
						display: block;
						font-weight: 600;
						font-size: 13px;
						font-style: normal;
						color: #666;
						line-height: 1.5em;
					}
					.full-price-open-popup {
						display: inline-block;
						font-size: 13px;
						color: $color;
						text-decoration: underline;
						margin-top: 8px;
						&:hover {
							color: #333;
						}
					}
				}
			}
		}
	}
}
.good-to-know-table,
.facilities-freebies-table {
	min-width: 644px;
	tbody {
		tr {
			border-bottom: 1px solid #D4D4D4;
			p {
				margin-bottom: 0;
				em {
					display: inline-block;
					color: $color;
					margin-left: 20px;
				}
			}
			th {
				font-size: 13px;
				font-weight: 600;
				color: #262626;
				padding: 18px 0;
				width: 35%;
				padding-right: 20px;
				p {
					position: relative;
					padding-left: 20px;
					&:before {
						content: '';
						display: inline-block;
						position: absolute;
						width: 6px;
						height: 6px;
						border-radius: 50%;
						background-color: $color;
						left: 0;
						top: 8px;
					}
				}
			}
			td {
				font-size: 13px;
				font-weight: 600;
				color: #777;
				padding: 18px 0;
				width: 65%;
				.light {
					display: inline-block;
					background-color: #55A649;
					color: #fff;
					padding: 0 10px;
					line-height: 1.8em;
					margin-top: 4px;
					margin-bottom: 4px;
					margin-right: 12px;
				}
			}
		}
	}
}
#reviews {
	.rating-info {
		overflow: hidden;
		margin-bottom: 50px;
		.average-rating-review {
			display: inline-block;
			overflow: hidden;
			min-width: 290px;
			float: left;
			.count {
				display: inline-block;
				width: 70px;
				height: 70px;
				border-radius: 4px;
				line-height: 70px;
				text-align: center;
				font-size: 32px;
				font-weight: 700;
				color: #fff;
				float: left;
				margin-right: 30px;
			}
			&.good {
				.count {
					background-color: #55A649;
				}
			}
			&.fine {
				.count {
					background-color: #F27C38;
				}
			}
			em {
				display: block;
				font-size: 12px;
				font-weight: 700;
				font-style: normal;
				color: $color;
				margin-top: 15px;
			}
			span {
				display: block;
				font-size: 16px;
				color: #666;
			}
		}
		.rating-review {
			display: inline-block;
			list-style: none;
			padding: 0;
			margin: 0;
			margin-top: 15px;
			li {
				display: inline-block;
				padding: 0 20px;
				em {
					display: block;
					font-weight: 700;
					font-size: 12px;
					font-style: normal;
					color: $color;
				}
				span {
					display: block;
					font-size: 16px;
					color: #666;
				}
			}
		}
		.write-review {
			display: inline-block;
			font-size: 12px;
			font-weight: 600;
			color: #fff;
			background-color: $color;
			line-height: 30px;
			padding: 0 12px;
			white-space: nowrap;
			border-radius: 3px;
			float: right;
			margin-top: 15px;
			&.write-review-active,
			&:hover {
				background-color: #444;
			}
		}
	}
	#add_review {
		background-color: #fff;
		border-radius: 4px;
		padding: 20px;
		display: none;
		.comment-reply-title {
			font-size: 18px;
			font-weight: 600;
			color: #444;
			margin-top: 0;
		}
		form {
			> div {
				label {
					display: block;
					font-size: 13px;
					font-weight: 600;
					color: #666;
					margin-bottom: 8px;
					.required {
						color: #FF6666;
					}
				}
			}
			.comment-form-author,
			.comment-form-email {
				width: 50%;
				margin-bottom: 20px;
				input {
					width: 100%;
					background-color: #eee;
				}
			}
			.comment-form-rating {
				width: 50%;
				padding-top: 10px;
				h4 {
					border: 0;
					font-size: 13px;
					font-weight: 600;
					margin-bottom: 10px;
				}
				.comment-form-rating__content {
					overflow: hidden;
					margin-left: -10px;
					margin-right: -10px;
				}
				.item {
					width: 25%;
					display: inline-block;
					float: left;
					padding: 0 10px;
					margin-bottom: 15px;
					label {
						color: $color;
					}
					.awe-select-wrapper {
						width: 100%;
						select {
							width: 100%;
							background-color: #eee;
						}
						.fa {
							background-color: #eee;
						}
					}
				}
			}
			.comment-form-comment {
				textarea {
					width: 100%;
					height: 140px;
					background-color: #eee;
				}
			}
			.form-submit {
				.submit {
					display: inline-block;
					font-size: 13px;
					font-weight: 600;
					color: #fff;
					background-color: $color;
					line-height: 36px;
					padding: 0 12px;
					white-space: nowrap;
					border-radius: 3px;
					border: 0;
					margin-top: 15px;
					min-width: 120px;
					@include transition;
					&:hover {
						background-color: #444;
					}
				}
			}
		}
	}
	#comments {
		.commentlist {
			list-style: none;
			margin: 0;
			padding: 0;
			li {
				.comment-box {
					padding: 20px;
					background-color: #fff;
					margin-top: 10px;
					.avatar {
						width: 50px;
						height: 50px;
						overflow: hidden;
						border-radius: 4px;
						float: left;
						margin-right: 30px;
					}
					.comment-body {
						.meta {
							overflow: hidden;
							margin-top: 0;
							strong {
								font-size: 16px;
								font-weight: 700;
								color: #666;
							}
							.time {
								font-size: 12px;
								color: #A5A5A5;
								float: right;
							}
						}
						.description {
							font-size: 14px;
							color: #A6A6A6;
							p {
								margin: 0;
							}
						}
						.rating-info {
							margin-top: 20px;
							margin-bottom: 0;
							.average-rating-review {
								min-width: 195px;
								.count {
									width: 40px;
									height: 40px;
									line-height: 40px;
									font-size: 16px;
									margin-right: 10px;
									color: #fff;
								}
								em {
									margin-top: 0;
									color: #666;
								}
								span {
									color: #A6A6A6;
								}
							}
							.rating-review {
								margin-top: 0;
								li {
									em {
										color: #666;
									}
									span {
										color: #A6A6A6;
									}
								}
							}
						}
					}
				}
			}
		}
	}
}

.initiative__item {
	margin-bottom: 50px;
	&:last-child {
		margin-bottom: 0;
	}
}
.initiative-top {
	overflow: hidden;
	.title {
		float: left;
		.from-to {
			font-size: 18px;
			font-weight: 600;
			color: #333;
			.awe-icon {
				font-size: 10px;
				margin-left: 3px;
				margin-right: 3px;
			}
		}
		.time {
			font-size: 13px;
			color: #A5A5A5;
			margin-top: 3px;
		}
	}
	.price {
		float: right;
		text-align: right;
		.amount {
			display: block;
			font-size: 18px;
			font-weight: 600;
			color: #666;
		}
		a {
			display: block;
			font-size: 13px;
			color: $color;
			text-decoration: underline;
			&:hover {
				color: #444;
			}
		}
	}
}
.initiative-table {
	background-color: #fff;
	width: 100%;
	margin-top: 16px;
	margin-bottom: 10px;
	border-radius: 6px;
	tbody {
		tr {
			border-bottom: 2px solid #D4D4D4;
			&:last-child {
				border: 0;
			}
			th, td {
				padding: 20px;
			}
			th {
				width: 170px;
			}
			.item-thumb {
				position: relative;
				text-align: center;
				&:after {
					content: '';
					display: block;
					position: absolute;
					width: 0;
					height: 100%;
					border-right: 2px dotted #D4D4D4;
					top: 0;
					right: -20px;
				}
				.text {
					margin-top: 8px;
					span {
						font-size: 12px;
						font-weight: 400;
						color: #A5A5A5;
					}
					p {
						font-size: 18px;
						font-weight: 700;
						color: $color;
						margin: 0;
					}
				}
			}
			.item-body {
				padding: 0 30px;
				font-size: 0;
				overflow: hidden;
				.item-from,
				.item-time,
				.item-to {
					display: inline-block;
					width: 33.3%;
					padding: 0 20px;
					text-align: center;
					vertical-align: middle;
				}
				.item-time {
					.fa {
						display: block;
						font-size: 20px;
						color: #A6A6A6;
					}
					span {
						display: inline-block;
						font-weight: 600;
